.menu_nav {
  height: 100vh;
  width: 100%;
  background-color: #d12d35;
  padding: 40px;
  box-sizing: border-box;
  color: white;
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  text-transform: uppercase;
  padding-left: 100px;
}
html {
  scroll-behavior: smooth;
}
.menu_header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.menu_text h2 {
  font-family: Cinzel;
  font-weight: 700;
  font-size: 40px;
  line-height: 80%;
  letter-spacing: -1.5px;
  text-align: center;
  vertical-align: middle;
}

.menu_close {
  background: none;
  border: none;
  font-size: 28px;
  color: white;
  cursor: pointer;
}

.menu_content {
  display: flex;
  flex-direction: column;
  gap: 30px;
  margin-top: 40px;
}

.menu_content a {
  text-decoration: none;
  color: #ffffff;
  font-family: Cinzel;
  font-weight: 700;
  font-size: 22px;
  text-transform: uppercase;
}

.menu_content a span {
  position: relative;
  display: inline-block;
}

.menu_content a span::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #ffffff;
  transition: width 0.3s ease;
}

.menu_content a:hover span::after {
  width: 100%;
}

.menu_info {
  font-family: Cinzel;
  font-weight: 700;
  font-size: 16px;
  line-height: 27px;
  letter-spacing: 0px;
  vertical-align: middle;
  text-transform: uppercase;
  padding-top: 40px;
}

.menu_info-second {
  margin-top: 10px;
  margin-bottom: 30px; /* <- добавлен отступ вниз */
}

.menu_call {
  font-family: Cinzel;
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
  vertical-align: middle;
  text-transform: uppercase;
  border-bottom: 1px solid #fff;
}
.hidden {
  display: none !important;
}
.menu_phone {
  color: white;
  text-decoration: none;
  cursor: pointer;
  transition: text-decoration 0.3s;

  &:hover {
    text-decoration: underline;
  }
}
