.img_header {
  width: 100%;
  height: 100vh;
  background-image: url('../imgs/img_b.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  @media (max-width: 1260px) {
    background-size: cover;
    background-position: center;
    height: 100vh;
  }

  @media (max-width: 744px) {
    background-size: cover;
    background-position: center;
    height: 100vh;
  }
}

.header_block {
  display: flex;
  justify-content: space-between;
  padding-top: $header-padding-top;
  color: $color-white;
  position: relative;
}

.header_right_all {
  display: flex;
  gap: $gap-large;
}

.header_text {
  font-size: $font-xl;
  font-weight: $font-bold;
  line-height: 1;
}

.header_right {
  display: flex;
  align-items: center;
  gap: $gap-large;

  .icon {
    width: $icon-size;
    height: $icon-size;
  }
  .icon-cursor {
    cursor: pointer;
  }
}

.header_contacts {
  display: none;
  color: $color-white;
  font-size: $font-xs;
  line-height: 1.4;
  flex-direction: column;
  gap: $gap-small;
  position: absolute;
  right: 130px;
  max-width: $contacts-max-width;
  cursor: pointer;

  .contact_title {
    font-family: Cinzel;
    font-weight: 700;
    font-size: 16px;
    line-height: 27px;
    letter-spacing: 0px;
    vertical-align: middle;
    text-transform: uppercase;
  }

  .contact_phone {
    color: #fff;
    margin-bottom: 16px;
    font-size: 16px;
    line-height: 27px;
    text-decoration: none;
    display: block;
  }
}

.main_block {
  flex: 1;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding-top: 200px;
}

.block_description {
  font-family: 'Cinzel', serif;
  font-weight: 700;
  font-size: 80px;
  line-height: 80px;
  letter-spacing: -1.5px;
  text-align: center;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 498px;
}
@media (max-width: 1260px) {
  .main_block {
    padding-top: 120px;
    align-items: center;
    justify-content: center;
  }

  .block_description {
    font-family: 'Cinzel', serif;
    font-weight: 700;
    font-size: 64px;
    line-height: 64px;
    letter-spacing: -1.5px;
    text-align: center;
    vertical-align: middle;
    max-width: 100%;
    padding: 0 20px;
  }
}
@media (max-width: 744px) {
  .header_block {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding-top: 10px;
    position: relative;
    gap: 12px;
  }
  .header_text {
    font-size: 32px;
  }
  .header_contacts {
    right: 0;
    top: 70px;
  }
  .main_block {
    padding-top: 150px;
    align-items: center;
    justify-content: center;
  }

  .block_description {
    font-family: 'Cinzel', serif;
    font-weight: 700;
    font-size: 40px;
    line-height: 100%;
    letter-spacing: -1.5px;
    text-align: center;
    vertical-align: middle;
    max-width: 100%;
    padding: 0 15px;
  }
}
